import React from 'react'
import { useNavigate } from "react-router-dom";
import axios from 'axios';
import { useEffect, useState } from "react";
import { toast } from 'react-hot-toast';

export default function Profile() {
  const navigate = useNavigate();
  const [data, setData] = useState({});
  const [editData, setEditData] = useState({});
  const [isEditing, setIsEditing] = useState(false);
  const [isSameEmail, setIsSameEmail] = useState(true);
  const [isSamePhone, setIsSamePhone] = useState(true);

  useEffect(() => {
    if (editData.email === data.email) {
      setIsSameEmail(true);
    } else {
      setIsSameEmail(false);
    }

    if (editData.telefon === data.telefon) {
      setIsSamePhone(true);
    } else {
      setIsSamePhone(false);
    }
  }, [editData, data]);

  const handleSave = (e) => {
    e.preventDefault();

    const jmenoRegex = /^[a-zA-Z-]+$/;
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    editData.telefon = editData.telefon.replace(/\s+/g, '');

    if (!editData.jmeno || !jmenoRegex.test(editData.jmeno)) {
      toast.error('Jmno mus bt vyplnn a obsahovat pouze psmena bez mezer.');
    } else if (!editData.prijmeni || !jmenoRegex.test(editData.prijmeni)) {
      toast.error('Pjmen mus bt vyplnn a obsahovat pouze psmena bez mezer.');
    } else if (!/^\+\d{1,4}\d{9,15}$/.test(editData.telefon)) {
      toast.error('Telefonn slo mus bt mezi 9 a 15 slicemi a obsahovat telefonn pedvolbu mezi 1 a 4 slicemi.');
    } else if (!editData.email) {
      toast.error('Prosm vyplt email.');
    } else if (!emailRegex.test(editData.email)) {
      toast.error('Zadejte prosm email ve sprvnm formtu. (example@gmail.com)')
    } else if (!editData.kraj) {
      toast.error('Prosm, vyberte kraj.')
    } else if (!editData.mesto) {
      toast.error('Prosm, vypite msto.')
    } else {
      axios.post('http://localhost:3001/profile', { editData, isSameEmail, isSamePhone }, { withCredentials: true })
        .then(res => {
          if (res.data.Status === "Success") {
            setData(editData);
            setIsEditing(false);
            toast.success("Profil byl spn aktualizovn.");
          } else {
            toast.error(res.data.Error);
          }
        })
        .catch(error => {
          toast.error(error);
        });
    }
  };

  const handleChange = (e) => {
    setEditData({ ...editData, [e.target.name]: e.target.value });
  };

  const handleCancel = () => {
    setEditData(data);
    setIsEditing(false);
  };

  useEffect(() => {
    axios.get('http://localhost:3001/profile', { withCredentials: true })
      .then(res => {
        if (res.data.Status === "Success") {
          setData(res.data);
          setEditData(res.data);
        } else {
          navigate('/Login');
        }
      })
      .catch(error => {
        toast.error("Dolo k chyb pi ntn strnky profilu. " + error);
      });
  }, [])


  const renderInputField = (label, name, value) => (
    <div className="row align-items-center" style={{ marginBottom: 30 }}>
      <label htmlFor={name} className="col-sm-2 col-form-label fs-4">{label}</label>
      <div className="col-sm-10">
        <input
          id={name}
          name={name}
          type="text"
          className="form-control"
          disabled={!isEditing}
          value={isEditing ? editData[name] || "" : value || ""}
          onChange={handleChange}
        />
      </div>
    </div>
  );

  return (
    <>
      <div className="container">
        <div className="row justify-content-center">
          <div className='col col-xxl-8 col-xl-8 col-lg-8'>
            <div className='row'>
              <div className='col-12 col-xxl-4 col-xl-4 col-lg-12 col-md-12 col-sm-12 text-center text-xl-start'>
                <svg xmlns="http://www.w3.org/2000/svg" width="200" height="200" fill="currentColor" className="bi bi-person-circle" viewBox="0 0 16 16">
                  <path d="M11 6a3 3 0 1 1-6 0 3 3 0 0 1 6 0" />
                  <path fillRule="evenodd" d="M0 8a8 8 0 1 1 16 0A8 8 0 0 1 0 8m8-7a7 7 0 0 0-5.468 11.37C3.242 11.226 4.805 10 8 10s4.757 1.225 5.468 2.37A7 7 0 0 0 8 1" />
                </svg>
              </div>
              <div className='col col-xxl-8 col-xl-8 col-lg-12'>
                <h2 className='fw-bold' style={{ marginTop: 40, marginBottom: 40 }}>{data.jmeno} {data.prijmeni}</h2>
                <h3>
                  <span className='text-primary'>{data.email}</span>
                  <span> - {data.role}</span>
                </h3>
              </div>
            </div>
            <h2 className='fw-bold' style={{ marginTop: 30, marginBottom: 30 }}>et</h2>
            <form onSubmit={handleSave}>
              {renderInputField("Jmno", "jmeno", data.jmeno)}
              {renderInputField("Pjmen", "prijmeni", data.prijmeni)}
              {renderInputField("Telefon", "telefon", data.telefon)}
              {renderInputField("Email", "email", data.email)}
              <div className='row align-items-center' style={{ marginBottom: 30 }}>
                <label htmlFor="jmeno" className="col-sm-2 col-form-label fs-4">Kraj</label>
                <div className="col-sm-10">
                  <select
                    id="kraj"
                    name="kraj"
                    className="form-select"
                    value={isEditing ? editData.kraj || "" : data.kraj || ""}
                    disabled={!isEditing}
                    onChange={handleChange}
                  >
                    <option value='' disabled>Vyberte kraj</option>
                    <option value="Praha">Hlavn msto Praha</option>
                    <option value="Jihoesk">Jihoesk kraj</option>
                    <option value="Jihomoravsk">Jihomoravsk kraj</option>
                    <option value="Karlovarsk">Karlovarsk kraj</option>
                    <option value="Krlovhradeck">Krlovhradeck kraj</option>
                    <option value="Libereck">Libereck kraj</option>
                    <option value="Moravskoslezsk">Moravskoslezsk kraj</option>
                    <option value="Olomouck">Olomouck kraj</option>
                    <option value="Pardubick">Pardubick kraj</option>
                    <option value="Plzesk">Plzesk kraj</option>
                    <option value="Stedoesk">Stedoesk kraj</option>
                    <option value="steck">steck kraj</option>
                    <option value="Vysoina">Kraj Vysoina</option>
                    <option value="Zlnsk">Zlnsk kraj</option>
                  </select>
                </div>
              </div>
              {renderInputField("Msto", "mesto", data.mesto)}

              <div>
                {isEditing ? (
                  <div>
                    <button type='submit' className='btn-border-radius-lg form-control btn btn-outline-secondary' style={{ marginBottom: 10 }}>Uloit</button>
                    <button className='btn-border-radius-lg form-control btn btn-outline-secondary' onClick={handleCancel} style={{ marginBottom: 30 }}>Zruit</button>
                  </div>
                ) : (
                  <button className='btn-border-radius-lg form-control btn btn-outline-secondary' onClick={() => setIsEditing(true)} style={{ marginBottom: 30 }}>Upravit</button>
                )}

              </div>
            </form>
          </div>
        </div>
      </div >
    </>
  )
}
